import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, AlertController } from 'ionic-angular';
import { Http } from '@angular/http';
import { AppConfig } from '../../../app/app.config';
import 'rxjs/add/operator/map';
/**
 * Generated class for the ActicitymorePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
    selector: 'page-acticitymore',
    templateUrl: 'acticitymore.html',
})
export class ActicitymorePage {
    url: any;
    html: any;

    constructor(public navCtrl: NavController, public navParams: NavParams, public http: Http, public loadingCtrl: LoadingController, public alertCtrl: AlertController) {
        this.url = AppConfig.getUrl() + "/xfwSpider/shyk_view?cookie=" + localStorage.xfwcookie + "&shykType=" + this.navParams.data.shykType+ "&id=" + this.navParams.data.id;
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad ActicitymorePage');
        let loading = this.loadingCtrl.create({
            content: '加载中...'
        });
        loading.present();
        return new Promise((resolve, reject) => {
            this.http.get(this.url)
                .map(res => res.json())
                .subscribe(data => {
                    resolve(data);
                    if (data.status) {
                        this.html = data.data;
                    } else {
                        let alert = this.alertCtrl.create({
                            title: data.msg
                        });
                        alert.present();
                    }
                }, err => {
                    console.log(err);
                })
            loading.dismiss();
        })
    }

}



